<template>
    <main-layout
        :settings-breadcrumbs="settingsBreadcrumbs"
    >
        <template #content>
            <profile-form-layout
                :friends="true"
                :show-modal="showModal"
                @validate="validate"
            >
                <template #headerIcon>
                    <email-outline />
                </template>
                <template #form>
                    <div class="profile-info__form mt-7">
                        <div class="invite-friends_body-attention">
                            To invite your friend, enter your friend's e-mail here. We will send an e-mail
                            invitation and once your friend registers on our site, you will see the status
                            update to REGISTERED in the table on the previous screen. It is all automatic!
                        </div>
                        <div class="profile-info__form-group">
                            <div class="profile-info__form-row">
                                <div class="profile-info__col50">
                                    <form-input
                                        v-model="emailValue"
                                        legend="Email address"
                                        prepend-icon="EmailOutlineIcon"
                                        type="text"
                                        name="email"
                                        :field.sync="emailValue"
                                        :errors="validationMessage($v.email)"
                                        :is-valid="$v.email.$dirty && !$v.email.$anyError"
                                        @input.native="$v.email.$touch()"
                                        @blur.native="$v.email.$touch()"
                                    />
                                </div>
                                <div class="profile-info__col50">
                                    <form-input
                                        v-model="$v.email2.$model"
                                        legend="Email address"
                                        prepend-icon="EmailOutlineIcon"
                                        type="text"
                                        name="email"
                                        :field.sync="email2"
                                        :errors="validationMessage($v.email2)"
                                        :is-valid="$v.email2.$dirty && !$v.email2.$anyError"
                                        @input.native="$v.email2.$touch()"
                                        @blur.native="$v.email2.$touch()"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </template>
            </profile-form-layout>
        </template>
    </main-layout>
</template>

<script>
import MainLayout from '@/layouts/MainLayout';
import ProfileFormLayout from '@/layouts/profile/ProfileFormLayout';
import EmailOutline from 'mdi-vue/EmailOutline'
import FormInput from '@/components/common/form-elements/FormInput';
import { eventBus } from '@/helpers/event-bus/'
import { debounce } from 'lodash'
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { formMessages } from '@/validation/friends/Messages'
import { validationRules } from '@/validation/friends/Rules.js'

import { createNamespacedHelpers } from 'vuex';
import {
    ACTION_CLIENT_CHECK_EMAIL,
    ACTION_CLIENT_FRIEND_ADD
} from '@/store/modules/client/action-types'

const { mapActions: mapClientActions } = createNamespacedHelpers('client');

export default {
    components: {
        MainLayout,
        ProfileFormLayout,
        EmailOutline,
        FormInput
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data() {
        return {
            emailValue: '',
            email: '',
            email2: '',
            showModal: false
        }
    },
    computed: {
        settingsBreadcrumbs() {
            return {
                title: 'Add A Friend',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'Account',
                        url: '/cp/account'
                    },
                    {
                        title: 'My Friends',
                        url: '/cp/friends'
                    },
                    {
                        title: 'Add',
                        url: '/cp/friends-new'
                    }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            }
        }
    },
    watch: {
        emailValue: debounce(function (newEmail) {
            this.email = newEmail
        }, 300)
    },
    mounted() {
        eventBus.$on('saveSettings', () => {
            this.validate()
        })
    },
    beforeDestroy() {
        eventBus.$off('saveSettings')
    },
    methods: {
        ...mapClientActions([
            ACTION_CLIENT_CHECK_EMAIL,
            ACTION_CLIENT_FRIEND_ADD
        ]),
        async checkEmail(value) {
            await this[ACTION_CLIENT_CHECK_EMAIL](value)
        },
        async addFriend(value) {
            await this[ACTION_CLIENT_FRIEND_ADD](value)
        },
        async submit() {
            const vm = this
            vm.loadingSubmit = true
            try {
                await this.addFriend({
                    email: vm.email
                })
                this.$router.push({
                    name: 'friends'
                })
                eventBus.$emit('showSnackBar', 'A friends has been added.', 'success');
            } catch (e) {
                eventBus.$emit('showSnackBar', e.response.data.message, 'error');
            } finally {
                vm.loadingSubmit = false
            }
        },
        validationMessage: validationMessage(formMessages)
    }
}
</script>
